* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input[type=text]{
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card-grid {
  display: flex;  
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  min-height: 170px;
}